import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Box,
  Typography,
  Card,
  CardContent,
  Button,
  Chip,
  Avatar,
  Divider,
  Tabs,
  Tab,
  TextField,
  Alert,
  Grid,
  IconButton,
  Tooltip,
  Paper,
  List,
  ListItem,
  ListItemText,
  ListItemIcon,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  LinearProgress,
  Accordion,
  AccordionSummary,
  AccordionDetails
} from '@mui/material';
import {
  Code as CodeIcon,
  Description as DescriptionIcon,
  Public as PublicIcon,
  Lock as LockIcon,
  Star as StarIcon,
  PlayArrow as PlayIcon,
  ShoppingCart as BuyIcon,
  Person as PersonIcon,
  Schedule as ScheduleIcon,
  MonetizationOn as MonetizationIcon,
  Info as InfoIcon,
  CheckCircle as CheckCircleIcon,
  Warning as WarningIcon,
  ExpandMore as ExpandMoreIcon,
  Memory as MemoryIcon,
  Speed as SpeedIcon,
  Storage as StorageIcon,
  Timer as TimerIcon,
  Group as GroupIcon,
  Edit as EditIcon,
  Visibility as ViewIcon,
  Send as SendIcon,
  Reviews as ApproveIcon,
  Reject as RejectIcon
} from '@mui/icons-material';
import { formatDistanceToNow } from 'date-fns';
import ReactMarkdown from 'react-markdown';
import rehypeHighlight from 'rehype-highlight';
import { useAuth } from 'src/contexts/AuthContext';
import { projectService } from 'src/services/projectService';
import ComputingResourceAnalysis from './ComputingResourceAnalysis';
import ProjectUsageTracker from './ProjectUsageTracker';

const ProjectDetails = ({
  project,
  onClose,
  onRun,
  isOwner
}) => {
  const navigate = useNavigate();
  const [activeTab, setActiveTab] = useState(0);
  const [computingResources, setComputingResources] = useState(null);
  const [userUsage, setUserUsage] = useState(null);
  const [pendingVersions, setPendingVersions] = useState([]);
  const [openVersionDialog, setOpenVersionDialog] = useState(false);
  const [selectedVersion, setSelectedVersion] = useState(null);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    loadComputingResources();
    loadUserUsage();
    if (isOwner) {
      loadPendingVersions();
    }
  }, [project.id, isOwner]);

  const loadComputingResources = async () => {
    try {
      const response = await projectService.getComputingResources(project.id);
      setComputingResources(response.data);
    } catch (error) {
      console.error('Error loading computing resources:', error);
    }
  };

  const loadUserUsage = async () => {
    try {
      const response = await projectService.getUserUsage(project.id);
      setUserUsage(response.data);
    } catch (error) {
      console.error('Error loading user usage:', error);
    }
  };

  const loadPendingVersions = async () => {
    try {
      const response = await projectService.getPendingVersions(project.id);
      setPendingVersions(response.data);
    } catch (error) {
      console.error('Error loading pending versions:', error);
    }
  };

  const handleTabChange = (event, newValue) => {
    setActiveTab(newValue);
  };

  const handleRun = () => {
    navigate(`/project/${project.id}/run`);
  };

  const handleApproveVersion = async (versionId) => {
    try {
      setLoading(true);
      await projectService.approveVersion(project.id, versionId);
      await loadPendingVersions();
      setOpenVersionDialog(false);
    } catch (error) {
      console.error('Error approving version:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleRejectVersion = async (versionId, reason) => {
    try {
      setLoading(true);
      await projectService.rejectVersion(project.id, versionId, reason);
      await loadPendingVersions();
      setOpenVersionDialog(false);
    } catch (error) {
      console.error('Error rejecting version:', error);
    } finally {
      setLoading(false);
    }
  };

  const getProjectTypeIcon = (type) => {
    switch (type) {
      case 'code':
        return <CodeIcon sx={{ color: 'black' }} />;
      case 'text':
        return <DescriptionIcon sx={{ color: 'black' }} />;
      default:
        return <CodeIcon sx={{ color: 'black' }} />;
    }
  };

  const getStatusColor = (status) => {
    switch (status) {
      case 'approved':
        return 'success';
      case 'pending':
        return 'warning';
      case 'rejected':
        return 'error';
      case 'draft':
        return 'default';
      default:
        return 'default';
    }
  };

  const calculateMonthlyCost = () => {
    if (!computingResources) return 0;
    const monthlyServerCost = 20; // $20/month server cost
    const cpuPercentage = computingResources.cpu_usage;
    const memoryPercentage = computingResources.memory_usage;
    const avgUsage = (cpuPercentage + memoryPercentage) / 2;
    return (avgUsage / 100) * monthlyServerCost;
  };

  const latestVersion = project.latest_public_version || project.latest_private_version;

  return (
    <Box sx={{ width: '100%', maxHeight: '80vh', overflow: 'auto' }}>
      {/* Header */}
      <Box sx={{ mb: 3 }}>
        <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'flex-start', mb: 2 }}>
          <Box>
            <Typography variant="h4" component="h1" gutterBottom sx={{ color: 'black' }}>
              {project.title}
            </Typography>
            <Typography variant="body1" sx={{ mb: 2, color: 'black' }}>
              {project.description}
            </Typography>
          </Box>
          <Box sx={{ display: 'flex', gap: 1 }}>
            {project.is_public ? (
              <Chip icon={<PublicIcon sx={{ color: 'black' }} />} label="Public" color="primary" variant="outlined" />
            ) : (
              <Chip icon={<LockIcon sx={{ color: 'black' }} />} label="Private" color="default" variant="outlined" />
            )}
            <Chip
              icon={<CheckCircleIcon sx={{ color: 'black' }} />}
              label={project.status}
              color={getStatusColor(project.status)}
              variant="outlined"
            />
          </Box>
        </Box>

        {/* Action Buttons */}
        <Box sx={{ display: 'flex', gap: 2, mb: 3 }}>
          <Button
            variant="contained"
            startIcon={<PlayIcon sx={{ color: 'white' }} />}
            onClick={handleRun}
            sx={{ backgroundColor: 'black', color: 'white' }}
          >
            Run Project
          </Button>
          {isOwner && (
            <Button
              variant="outlined"
              startIcon={<EditIcon sx={{ color: 'black' }} />}
              onClick={() => navigate(`/project/${project.id}/edit`)}
              sx={{ color: 'black', borderColor: 'black' }}
            >
              Edit Project
            </Button>
          )}
        </Box>
      </Box>

      {/* Tabs */}
      <Box sx={{ borderBottom: 1, borderColor: 'divider', mb: 2 }}>
        <Tabs value={activeTab} onChange={handleTabChange}>
          <Tab label="Content" sx={{ color: 'black' }} />
          <Tab label="Usage" sx={{ color: 'black' }} />
          <Tab label="Computing Resources" sx={{ color: 'black' }} />
          <Tab label="Analytics" sx={{ color: 'black' }} />
          {isOwner && <Tab label="Collaboration" sx={{ color: 'black' }} />}
        </Tabs>
      </Box>

      {/* Tab Content */}
      <Box sx={{ mt: 2 }}>
        {/* Content Tab */}
        {activeTab === 0 && (
          <Card>
            <CardContent>
              <Typography variant="h6" gutterBottom sx={{ color: 'black' }}>
                Project Content
              </Typography>
              <Paper
                sx={{
                  p: 2,
                  backgroundColor: '#f5f5f5',
                  fontFamily: 'monospace',
                  whiteSpace: 'pre-wrap',
                  overflow: 'auto',
                  maxHeight: '400px'
                }}
              >
                {project.project_type === 'code' ? (
                  <div style={{ color: 'black' }}>
                    <ReactMarkdown rehypePlugins={[rehypeHighlight]}>
                      {latestVersion?.content || 'No content available'}
                    </ReactMarkdown>
                  </div>
                ) : (
                  <Typography variant="body1" sx={{ color: 'black' }}>
                    {latestVersion?.content || 'No content available'}
                  </Typography>
                )}
              </Paper>
            </CardContent>
          </Card>
        )}

        {/* Usage Tab */}
        {activeTab === 1 && (
          <Grid container spacing={3}>
            <Grid item xs={12} md={6}>
              <Card>
                <CardContent>
                  <Typography variant="h6" gutterBottom sx={{ color: 'black' }}>
                    Usage Instructions
                  </Typography>
                  {latestVersion?.usage_instructions ? (
                    <div style={{ color: 'black' }}>
                      <ReactMarkdown>
                        {latestVersion.usage_instructions}
                      </ReactMarkdown>
                    </div>
                  ) : (
                    <Typography sx={{ color: 'black' }}>
                      No usage instructions available.
                    </Typography>
                  )}
                </CardContent>
              </Card>
            </Grid>
            <Grid item xs={12} md={6}>
              <ProjectUsageTracker 
                project={project} 
                onUpdate={loadUserUsage}
              />
            </Grid>
          </Grid>
        )}

        {/* Computing Resources Tab */}
        {activeTab === 2 && (
          <ComputingResourceAnalysis 
            project={project} 
            onUpdate={loadComputingResources}
          />
        )}

        {/* Analytics Tab */}
        {activeTab === 3 && (
          <Grid container spacing={3}>
            <Grid item xs={12} md={6}>
              <Card>
                <CardContent>
                  <Typography variant="h6" gutterBottom sx={{ color: 'black' }}>
                    Project Analytics
                  </Typography>
                  <Box sx={{ mb: 2 }}>
                    <Typography variant="body2" sx={{ color: 'black', mb: 1 }}>
                      Total Views: {project.total_views || 0}
                    </Typography>
                    <Typography variant="body2" sx={{ color: 'black', mb: 1 }}>
                      Total Runs: {project.total_runs || 0}
                    </Typography>
                    <Typography variant="body2" sx={{ color: 'black', mb: 1 }}>
                      Total Purchases: {project.total_purchases || 0}
                    </Typography>
                    <Typography variant="body2" sx={{ color: 'black', mb: 1 }}>
                      Total Revenue: ${project.total_revenue || 0}
                    </Typography>
                  </Box>
                </CardContent>
              </Card>
            </Grid>
            {userUsage && (
              <Grid item xs={12} md={6}>
                <Card>
                  <CardContent>
                    <Typography variant="h6" gutterBottom sx={{ color: 'black' }}>
                      Your Usage
                    </Typography>
                    <Box sx={{ mb: 2 }}>
                      <Typography variant="body2" sx={{ color: 'black', mb: 1 }}>
                        Times Used: {userUsage.usage_count}
                      </Typography>
                      <Typography variant="body2" sx={{ color: 'black', mb: 1 }}>
                        Total Cost: ${userUsage.total_cost}
                      </Typography>
                      <Typography variant="body2" sx={{ color: 'black', mb: 1 }}>
                        Last Used: {formatDistanceToNow(new Date(userUsage.last_used), { addSuffix: true })}
                      </Typography>
                    </Box>
                  </CardContent>
                </Card>
              </Grid>
            )}
          </Grid>
        )}

        {/* Collaboration Tab (Owner Only) */}
        {activeTab === 4 && isOwner && (
          <Card>
            <CardContent>
              <Typography variant="h6" gutterBottom sx={{ color: 'black' }}>
                Collaboration Management
              </Typography>
              
              {/* Pending Versions */}
              <Accordion>
                <AccordionSummary expandIcon={<ExpandMoreIcon />}>
                  <Typography sx={{ color: 'black' }}>
                    Pending Versions ({pendingVersions.length})
                  </Typography>
                </AccordionSummary>
                <AccordionDetails>
                  {pendingVersions.length > 0 ? (
                    <List>
                      {pendingVersions.map((version) => (
                        <ListItem key={version.id} divider>
                          <ListItemIcon>
                            <EditIcon sx={{ color: 'black' }} />
                          </ListItemIcon>
                          <ListItemText
                            primary={`Version ${version.version_number} by ${version.created_by.first_name} ${version.created_by.last_name}`}
                            secondary={formatDistanceToNow(new Date(version.created_at), { addSuffix: true })}
                            sx={{ '& .MuiListItemText-primary': { color: 'black' } }}
                          />
                          <Box sx={{ display: 'flex', gap: 1 }}>
                            <Button
                              size="small"
                              color="success"
                              startIcon={<ApproveIcon />}
                              onClick={() => {
                                setSelectedVersion(version);
                                setOpenVersionDialog(true);
                              }}
                            >
                              Review
                            </Button>
                          </Box>
                        </ListItem>
                      ))}
                    </List>
                  ) : (
                    <Typography sx={{ color: 'black' }}>
                      No pending versions to review.
                    </Typography>
                  )}
                </AccordionDetails>
              </Accordion>
            </CardContent>
          </Card>
        )}
      </Box>

      {/* Version Review Dialog */}
      <Dialog
        open={openVersionDialog}
        onClose={() => setOpenVersionDialog(false)}
        maxWidth="md"
        fullWidth
      >
        <DialogTitle sx={{ color: 'black' }}>
          Review Version {selectedVersion?.version_number}
        </DialogTitle>
        <DialogContent>
          {selectedVersion && (
            <Box>
              <Typography variant="body2" sx={{ mb: 2, color: 'black' }}>
                Submitted by: {selectedVersion.created_by.first_name} {selectedVersion.created_by.last_name}
              </Typography>
              <Typography variant="body2" sx={{ mb: 2, color: 'black' }}>
                Date: {formatDistanceToNow(new Date(selectedVersion.created_at), { addSuffix: true })}
              </Typography>
              <Typography variant="h6" sx={{ mb: 2, color: 'black' }}>
                Content Changes:
              </Typography>
              <Paper
                sx={{
                  p: 2,
                  backgroundColor: '#f5f5f5',
                  fontFamily: 'monospace',
                  whiteSpace: 'pre-wrap',
                  overflow: 'auto',
                  maxHeight: '300px'
                }}
              >
                <Typography variant="body2" sx={{ color: 'black' }}>
                  {selectedVersion.content}
                </Typography>
              </Paper>
            </Box>
          )}
        </DialogContent>
        <DialogActions>
          <Button 
            onClick={() => setOpenVersionDialog(false)}
            sx={{ color: 'black' }}
          >
            Cancel
          </Button>
          <Button
            onClick={() => handleRejectVersion(selectedVersion?.id, 'Rejected by owner')}
            color="error"
            variant="outlined"
            disabled={loading}
          >
            Reject
          </Button>
          <Button
            onClick={() => handleApproveVersion(selectedVersion?.id)}
            color="success"
            variant="contained"
            disabled={loading}
          >
            Approve
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
};

export default ProjectDetails; 