import React, { useState, useEffect } from 'react';
import {
    Box,
    Card,
    CardContent,
    Typography,
    LinearProgress,
    Grid,
    Chip,
    Alert,
    Button,
    Dialog,
    DialogTitle,
    DialogContent,
    DialogActions,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Paper,
    IconButton,
    Tooltip
} from '@mui/material';
import {
    Memory as MemoryIcon,
    Speed as SpeedIcon,
    Storage as StorageIcon,
    Timer as TimerIcon,
    MonetizationOn as CostIcon,
    Info as InfoIcon,
    Refresh as RefreshIcon,
    TrendingUp as TrendingIcon,
    Warning as WarningIcon,
    CheckCircle as CheckIcon
} from '@mui/icons-material';
import { projectService } from 'src/services/projectService';

const ComputingResourceAnalysis = ({ project, onUpdate }) => {
    const [resources, setResources] = useState(null);
    const [loading, setLoading] = useState(false);
    const [showDetails, setShowDetails] = useState(false);
    const [error, setError] = useState(null);

    useEffect(() => {
        loadResources();
    }, [project.id]);

    const loadResources = async () => {
        try {
            setLoading(true);
            setError(null);
            const response = await projectService.getComputingResources(project.id);
            setResources(response.data);
        } catch (error) {
            console.error('Error loading computing resources:', error);
            setError('Failed to load computing resources');
        } finally {
            setLoading(false);
        }
    };

    const analyzeResources = async () => {
        try {
            setLoading(true);
            setError(null);
            const response = await projectService.analyzeComputingResources(project.id, project.latest_version?.content || '');
            setResources(response.data);
            if (onUpdate) onUpdate();
        } catch (error) {
            console.error('Error analyzing resources:', error);
            setError('Failed to analyze computing resources');
        } finally {
            setLoading(false);
        }
    };

    const calculateMonthlyCost = () => {
        if (!resources) return 0;
        const monthlyServerCost = 20; // $20/month server cost
        const cpuPercentage = resources.cpu_usage || 0;
        const memoryPercentage = resources.memory_usage || 0;
        const avgUsage = (cpuPercentage + memoryPercentage) / 2;
        return (avgUsage / 100) * monthlyServerCost;
    };

    const getResourceIntensityColor = (intensity) => {
        switch (intensity) {
            case 'low':
                return 'success';
            case 'medium':
                return 'warning';
            case 'high':
                return 'error';
            default:
                return 'default';
        }
    };

    const getComplexityLevel = (score) => {
        if (score < 30) return 'Low';
        if (score < 70) return 'Medium';
        return 'High';
    };

    if (loading && !resources) {
        return (
            <Card>
                <CardContent>
                    <Box sx={{ textAlign: 'center', py: 3 }}>
                        <Typography variant="h6" sx={{ color: 'black', mb: 2 }}>
                            Analyzing Computing Resources...
                        </Typography>
                        <LinearProgress />
                    </Box>
                </CardContent>
            </Card>
        );
    }

    if (error) {
        return (
            <Card>
                <CardContent>
                    <Alert severity="error" sx={{ color: 'black' }}>
                        {error}
                    </Alert>
                    <Button
                        startIcon={<RefreshIcon />}
                        onClick={loadResources}
                        sx={{ mt: 2, color: 'black' }}
                    >
                        Retry
                    </Button>
                </CardContent>
            </Card>
        );
    }

    return (
        <>
            <Card>
                <CardContent>
                    <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 3 }}>
                        <Typography variant="h6" sx={{ color: 'black' }}>
                            Computing Resource Analysis
                        </Typography>
                        <Box sx={{ display: 'flex', gap: 1 }}>
                            <Tooltip title="View Detailed Analysis">
                                <IconButton
                                    size="small"
                                    onClick={() => setShowDetails(true)}
                                    sx={{ color: 'black' }}
                                >
                                    <InfoIcon />
                                </IconButton>
                            </Tooltip>
                            <Tooltip title="Re-analyze Resources">
                                <IconButton
                                    size="small"
                                    onClick={analyzeResources}
                                    disabled={loading}
                                    sx={{ color: 'black' }}
                                >
                                    <RefreshIcon />
                                </IconButton>
                            </Tooltip>
                        </Box>
                    </Box>

                    {resources ? (
                        <>
                            <Grid container spacing={3}>
                                {/* Resource Usage */}
                                <Grid item xs={12} md={6}>
                                    <Typography variant="subtitle1" sx={{ color: 'black', mb: 2 }}>
                                        Resource Usage
                                    </Typography>
                                    
                                    <Box sx={{ mb: 3 }}>
                                        <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
                                            <Typography variant="body2" sx={{ color: 'black' }}>
                                                CPU Usage
                                            </Typography>
                                            <Typography variant="body2" sx={{ color: 'black' }}>
                                                {resources.cpu_usage?.toFixed(1)}%
                                            </Typography>
                                        </Box>
                                        <LinearProgress 
                                            variant="determinate" 
                                            value={Math.min(resources.cpu_usage || 0, 100)} 
                                            sx={{ mb: 2 }}
                                        />
                                        
                                        <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
                                            <Typography variant="body2" sx={{ color: 'black' }}>
                                                Memory Usage
                                            </Typography>
                                            <Typography variant="body2" sx={{ color: 'black' }}>
                                                {resources.memory_usage?.toFixed(1)} MB
                                            </Typography>
                                        </Box>
                                        <LinearProgress 
                                            variant="determinate" 
                                            value={Math.min((resources.memory_usage || 0) / 10, 100)} 
                                            sx={{ mb: 2 }}
                                        />
                                        
                                        <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
                                            <Typography variant="body2" sx={{ color: 'black' }}>
                                                Execution Time
                                            </Typography>
                                            <Typography variant="body2" sx={{ color: 'black' }}>
                                                {resources.execution_time?.toFixed(2)}s
                                            </Typography>
                                        </Box>
                                        <LinearProgress 
                                            variant="determinate" 
                                            value={Math.min((resources.execution_time || 0) * 10, 100)} 
                                        />
                                    </Box>
                                </Grid>

                                {/* Cost Analysis */}
                                <Grid item xs={12} md={6}>
                                    <Typography variant="subtitle1" sx={{ color: 'black', mb: 2 }}>
                                        Cost Analysis
                                    </Typography>
                                    
                                    <Box sx={{ mb: 3 }}>
                                        <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
                                            <Typography variant="body2" sx={{ color: 'black' }}>
                                                Cost per Run
                                            </Typography>
                                            <Typography variant="body2" sx={{ color: 'green', fontWeight: 'bold' }}>
                                                ${resources.estimated_cost_per_run?.toFixed(4)}
                                            </Typography>
                                        </Box>
                                        
                                        <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
                                            <Typography variant="body2" sx={{ color: 'black' }}>
                                                Monthly Server Cost
                                            </Typography>
                                            <Typography variant="body2" sx={{ color: 'black' }}>
                                                $20.00
                                            </Typography>
                                        </Box>
                                        
                                        <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
                                            <Typography variant="body2" sx={{ color: 'black' }}>
                                                Your Project's Monthly Cost
                                            </Typography>
                                            <Typography variant="body2" sx={{ color: 'orange', fontWeight: 'bold' }}>
                                                ${calculateMonthlyCost().toFixed(2)}
                                            </Typography>
                                        </Box>
                                        
                                        <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
                                            <Typography variant="body2" sx={{ color: 'black' }}>
                                                Per-Use Charge
                                            </Typography>
                                            <Typography variant="body2" sx={{ color: 'black' }}>
                                                ${project.price_per_use}
                                            </Typography>
                                        </Box>
                                    </Box>

                                    {/* Complexity Score */}
                                    <Box sx={{ mb: 2 }}>
                                        <Typography variant="body2" sx={{ color: 'black', mb: 1 }}>
                                            Complexity Score: {resources.complexity_score || 0}/100
                                        </Typography>
                                        <Chip
                                            label={`${getComplexityLevel(resources.complexity_score || 0)} Complexity`}
                                            color={getResourceIntensityColor(resources.resource_intensity)}
                                            size="small"
                                            sx={{ mr: 1 }}
                                        />
                                        <Chip
                                            label={`${resources.resource_intensity} Intensity`}
                                            color={getResourceIntensityColor(resources.resource_intensity)}
                                            size="small"
                                            variant="outlined"
                                        />
                                    </Box>
                                </Grid>
                            </Grid>

                            {/* Cost Summary Alert */}
                            <Alert 
                                severity="info" 
                                sx={{ mt: 2, color: 'black' }}
                                icon={<CostIcon />}
                            >
                                Based on $20/month server cost, your project uses approximately {(calculateMonthlyCost() / 20 * 100).toFixed(1)}% of server resources.
                                {calculateMonthlyCost() > 5 && (
                                    <Typography variant="body2" sx={{ mt: 1, color: 'orange' }}>
                                        <WarningIcon sx={{ fontSize: 16, mr: 0.5, verticalAlign: 'middle' }} />
                                        High resource usage detected. Consider optimizing your code.
                                    </Typography>
                                )}
                            </Alert>
                        </>
                    ) : (
                        <Box sx={{ textAlign: 'center', py: 3 }}>
                            <Typography variant="body1" sx={{ color: 'black', mb: 2 }}>
                                No computing resource analysis available
                            </Typography>
                            <Button
                                variant="contained"
                                startIcon={<TrendingIcon />}
                                onClick={analyzeResources}
                                disabled={loading}
                                sx={{ backgroundColor: 'black', color: 'white' }}
                            >
                                Analyze Resources
                            </Button>
                        </Box>
                    )}
                </CardContent>
            </Card>

            {/* Detailed Analysis Dialog */}
            <Dialog
                open={showDetails}
                onClose={() => setShowDetails(false)}
                maxWidth="md"
                fullWidth
            >
                <DialogTitle sx={{ color: 'black' }}>
                    Detailed Computing Resource Analysis
                </DialogTitle>
                <DialogContent>
                    {resources && (
                        <Box>
                            <Grid container spacing={3}>
                                <Grid item xs={12}>
                                    <Typography variant="h6" sx={{ color: 'black', mb: 2 }}>
                                        Resource Breakdown
                                    </Typography>
                                    <TableContainer component={Paper}>
                                        <Table>
                                            <TableHead>
                                                <TableRow>
                                                    <TableCell sx={{ color: 'black', fontWeight: 'bold' }}>Resource</TableCell>
                                                    <TableCell sx={{ color: 'black', fontWeight: 'bold' }}>Usage</TableCell>
                                                    <TableCell sx={{ color: 'black', fontWeight: 'bold' }}>Percentage</TableCell>
                                                    <TableCell sx={{ color: 'black', fontWeight: 'bold' }}>Status</TableCell>
                                                </TableRow>
                                            </TableHead>
                                            <TableBody>
                                                <TableRow>
                                                    <TableCell sx={{ color: 'black' }}>
                                                        <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                                                            <SpeedIcon fontSize="small" />
                                                            CPU Usage
                                                        </Box>
                                                    </TableCell>
                                                    <TableCell sx={{ color: 'black' }}>
                                                        {resources.cpu_usage?.toFixed(1)}%
                                                    </TableCell>
                                                    <TableCell sx={{ color: 'black' }}>
                                                        {resources.cpu_usage?.toFixed(1)}%
                                                    </TableCell>
                                                    <TableCell>
                                                        <Chip
                                                            label={resources.cpu_usage > 70 ? 'High' : resources.cpu_usage > 30 ? 'Medium' : 'Low'}
                                                            color={resources.cpu_usage > 70 ? 'error' : resources.cpu_usage > 30 ? 'warning' : 'success'}
                                                            size="small"
                                                        />
                                                    </TableCell>
                                                </TableRow>
                                                <TableRow>
                                                    <TableCell sx={{ color: 'black' }}>
                                                        <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                                                            <MemoryIcon fontSize="small" />
                                                            Memory Usage
                                                        </Box>
                                                    </TableCell>
                                                    <TableCell sx={{ color: 'black' }}>
                                                        {resources.memory_usage?.toFixed(1)} MB
                                                    </TableCell>
                                                    <TableCell sx={{ color: 'black' }}>
                                                        {((resources.memory_usage || 0) / 1000 * 100).toFixed(1)}%
                                                    </TableCell>
                                                    <TableCell>
                                                        <Chip
                                                            label={resources.memory_usage > 700 ? 'High' : resources.memory_usage > 300 ? 'Medium' : 'Low'}
                                                            color={resources.memory_usage > 700 ? 'error' : resources.memory_usage > 300 ? 'warning' : 'success'}
                                                            size="small"
                                                        />
                                                    </TableCell>
                                                </TableRow>
                                                <TableRow>
                                                    <TableCell sx={{ color: 'black' }}>
                                                        <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                                                            <TimerIcon fontSize="small" />
                                                            Execution Time
                                                        </Box>
                                                    </TableCell>
                                                    <TableCell sx={{ color: 'black' }}>
                                                        {resources.execution_time?.toFixed(2)}s
                                                    </TableCell>
                                                    <TableCell sx={{ color: 'black' }}>
                                                        {((resources.execution_time || 0) / 30 * 100).toFixed(1)}%
                                                    </TableCell>
                                                    <TableCell>
                                                        <Chip
                                                            label={resources.execution_time > 20 ? 'High' : resources.execution_time > 10 ? 'Medium' : 'Low'}
                                                            color={resources.execution_time > 20 ? 'error' : resources.execution_time > 10 ? 'warning' : 'success'}
                                                            size="small"
                                                        />
                                                    </TableCell>
                                                </TableRow>
                                                <TableRow>
                                                    <TableCell sx={{ color: 'black' }}>
                                                        <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                                                            <StorageIcon fontSize="small" />
                                                            Storage Usage
                                                        </Box>
                                                    </TableCell>
                                                    <TableCell sx={{ color: 'black' }}>
                                                        {resources.storage_usage?.toFixed(1)} MB
                                                    </TableCell>
                                                    <TableCell sx={{ color: 'black' }}>
                                                        {((resources.storage_usage || 0) / 100 * 100).toFixed(1)}%
                                                    </TableCell>
                                                    <TableCell>
                                                        <Chip
                                                            label={resources.storage_usage > 70 ? 'High' : resources.storage_usage > 30 ? 'Medium' : 'Low'}
                                                            color={resources.storage_usage > 70 ? 'error' : resources.storage_usage > 30 ? 'warning' : 'success'}
                                                            size="small"
                                                        />
                                                    </TableCell>
                                                </TableRow>
                                            </TableBody>
                                        </Table>
                                    </TableContainer>
                                </Grid>

                                <Grid item xs={12}>
                                    <Typography variant="h6" sx={{ color: 'black', mb: 2 }}>
                                        Cost Analysis Details
                                    </Typography>
                                    <Alert severity="info" sx={{ color: 'black' }}>
                                        <Typography variant="body2" sx={{ mb: 1 }}>
                                            <strong>Cost Calculation Formula:</strong>
                                        </Typography>
                                        <Typography variant="body2" sx={{ fontFamily: 'monospace' }}>
                                            Cost per run = (CPU% + Memory%) × Execution Time × Monthly Server Cost ÷ (30 days × 24 hours × 3600 seconds)
                                        </Typography>
                                        <Typography variant="body2" sx={{ mt: 1 }}>
                                            <strong>Monthly Server Cost:</strong> $20.00
                                        </Typography>
                                        <Typography variant="body2">
                                            <strong>Your Project's Monthly Cost:</strong> ${calculateMonthlyCost().toFixed(2)}
                                        </Typography>
                                    </Alert>
                                </Grid>
                            </Grid>
                        </Box>
                    )}
                </DialogContent>
                <DialogActions>
                    <Button onClick={() => setShowDetails(false)} sx={{ color: 'black' }}>
                        Close
                    </Button>
                </DialogActions>
            </Dialog>
        </>
    );
};

export default ComputingResourceAnalysis; 